import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  CardText,
  Button,
  CardFooter,
  CardImg,
  CardSubtitle,
} from "reactstrap";
import "./index.scss";
import { connect } from "react-redux";
import { documentActions } from "../../store/file/file.action";
import ModalPopUp from "../modalPopup/modal-popup";
import Loader from "../loader/loader";
import environment from "../..";

class Download extends Component {
  state = {
    bgImg: require("../../assets/bglow.png"),
    error: null,
  };

  componentWillUnmount() {
    this.props.reset();
  }

  downloadIOS = () => {
    if (this.isMobile.iOS() && this.isMobile.safari()) {
      window.location.href =
        "itms-services://?action=download-manifest&url=" +
        environment.files.getIOS;
      return;
    }
    this.setState({ error: "Please download from IOS and safari browser..." });
  };

  downloadAndroid = () => {
    if (this.isMobile.Android()) {
      return this.props.downloadAndroid();
    }
    this.setState({ error: "Please download from android device..." });
  };

  isMobile = {
    Android: function () {
      const userAgent = navigator.userAgent || navigator.vendor || window.opera;
      return userAgent.match(/Android/i);
    },
    iOS: function () {
      const userAgent = navigator.userAgent || navigator.vendor || window.opera;
      return userAgent.match(/iPhone|iPad|iPod/i);
    },
    safari: function () {
      const userAgent = navigator.userAgent || navigator.vendor || window.opera;
      return userAgent.indexOf("Safari") !== -1;
    },
  };

  reset = () => {
    this.props.reset();
    this.setState({ error: null });
  };

  render() {
    const {
      downloadAndroidLoading,
      downloadIOSLoading,
      downloadIOSSuccess,
      downloadAndroidSuccess,
      downloadIOSError,
      downloadAndroidError,
    } = this.props;
    const localError = this.state.error;

    const loading = downloadAndroidLoading || downloadIOSLoading;
    const error = downloadAndroidError || downloadIOSError;
    const success = downloadIOSSuccess || downloadAndroidSuccess;
    const isOpen = localError || error || success;
    const popupMsg = success
      ? "APP DOWNLOADED SUCCESSFULLY"
      : localError
      ? localError
      : error
      ? "Error Occured"
      : null;
    const color = success ? "success" : error ? "danger" : "primary";
    return (
      <>
        <div className="bg">
          <img src={this.state.bgImg} />
        </div>
        {loading && <Loader />}
        <ModalPopUp
          modalOpen={isOpen}
          popupMsg={popupMsg}
          className={color}
          heading={"Download Status..."}
          toggle={this.reset}
        />
        <Container className="download-container d-flex align-items-center justify-content-center">
          <Row>
            <Col xs={12} sm={12}>
              <div className="app-logo d-inline-flex align-items-center">
                <img
                  width={70}
                  height={70}
                  src={require("../../assets/logo.png")}
                />
                <span
                  style={{
                    color: "#fff",
                    fontSize: "1.5rem",
                  }}
                >
                  Red Smart
                </span>
              </div>
              <div style={{ color: "#fff", marginBottom: "10px" }}>
                <CardTitle>
                  Get your{" "}
                  <span style={{ color: "red", fontWeight: "bold" }}>
                    FLIGHT
                  </span>{" "}
                  App
                </CardTitle>
                <CardSubtitle style={{ fontSize: "12px" }}>
                  This app for managing airasia flights
                </CardSubtitle>
              </div>
              <Row>
                <Col>
                  <Button
                    className="d-inline-flex align-items-center justify-content-center"
                    onClick={this.downloadAndroid}
                  >
                    <img
                      width={20}
                      height={20}
                      src={require("../../assets/android.svg")}
                    />
                    <span style={{ fontSize: "13px", paddingLeft: "5px" }}>
                      {" "}
                      Download For <br />
                      Android{" "}
                    </span>
                  </Button>
                </Col>
                <Col>
                  <Button
                    onClick={this.downloadIOS}
                    className="btn btn-danger d-inline-flex align-items-center justify-content-center"
                  >
                    <img
                      width={20}
                      height={20}
                      src={require("../../assets/apple.svg")}
                    />
                    <span style={{ fontSize: "13px", paddingLeft: "5px" }}>
                      {" "}
                      Download For <br /> IOS{" "}
                    </span>
                  </Button>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return state.file.fileReducer;
};

const actionCreators = {
  downloadAndroid: documentActions.downloadAndroid,
  downloadIOS: documentActions.downloadIOS,
  reset: documentActions.reset,
};
export default connect(mapStateToProps, actionCreators)(Download);
