import React, { Component } from "react";
import { Modal, ModalHeader, ModalFooter, ModalBody, Button } from "reactstrap";
import PropTypes from "prop-types";

class ModalPopUp extends Component {
  state = {};
  render() {
    const {
      modalOpen,
      toggle,
      className,
      popupMsg,
      heading,
      ...props
    } = this.props;
    return (
      <Modal
        isOpen={modalOpen ? true : false}
        toggle={toggle}
        className={`modal-${className}`}
        {...props}
      >
        <ModalHeader toggle={toggle}>{heading}</ModalHeader>
        <ModalBody>{popupMsg}</ModalBody>
        <ModalFooter>
          <Button color={className} onClick={toggle}>
            Done
          </Button>{" "}
        </ModalFooter>
      </Modal>
    );
  }
}

ModalPopUp.propTypes = {
  className: PropTypes.string,
  heading: PropTypes.any,
  popupMsg: PropTypes.any,
  modelOpen: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
  toggle: PropTypes.func,
};

ModalPopUp.defaultProps = {
  className: "",
  popupMsg: "",
  heading: "Info",
  modalOpen: false,
  toggle: () => {},
};

export default ModalPopUp;
